<template>
  <div class='lineup-box'>
    <div class="lineup-title">
      <van-icon class="user-icon mr-5" name="manager" />
      <span>周小森</span>
      <span class="sp2 ml-10">123123</span>
    </div>
    <div class="queueList">
      <ul class="queueListUl mt-10">
        <li class="queue-item" v-for="(item,index) in 6" :key="index">
          <div class="queue-title mb-10">
            <div>
              <van-icon class="user-icon mr-10" name="manager" />
              <span>候诊信息</span>
            </div>
            <div>
              <span class="sp1">智能提醒</span>
              <van-switch class="remind-switch" v-model="checked" size="18px" />
            </div>
          </div>
          <div class="queue-main">
            <p><span class="sp1">科室</span><span class="sp2">儿科</span></p>
            <p><span class="sp1">医生</span><span class="sp2">廖大神</span></p>
            <p><span class="sp1">您的候诊号序</span><span class="sp2"><span class="sp3 mr-5">5</span>号  还需等待<span class="sp3 ml-5 mr-5">2</span>人</span></p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
        checked:false
      }
    }
  };
</script>
<style lang='scss' scoped>
.lineup-box{
  .queueList{
    .queueListUl{
      padding: 0 10px;
      .queue-item{
        background: #fff;
        border-radius: 4px;
        padding: 10px;
        margin-bottom: 10px;
        .queue-title{
          align-items: center;
          display: flex;
          justify-content: space-between;
          i{margin-top: 2px;}
          span{font-size: 14px;}
          .remind-switch{
            position: relative;
            top: 4px;
          }
          .sp1{font-size: 10px;}
        }
        .queue-main{
          p{
            font-size: 14px;
            margin-bottom: 5px;
            padding-left: 10px;
            .sp1{
              display: inline-block;
              width: 100px;
              color: #999;
            }
            .sp3{color: #38A2FE}
          }
        }
        
      }
    }
  }
  .lineup-title{
    background: #fff;
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: center; /* 水平居中 */
    position: relative;
    .user-icon{
      margin-top: 2px;
      color: #38A2FE;
      font-size: 20px;  
    }
    .sp2{
      font-size: 12px;
      color: #999;
      margin-top: 3px;
    }
  }
  .lineup-title::after {
    position: absolute;
    box-sizing: border-box;
    content: ' ';
    pointer-events: none;
    right: 0;
    bottom: 0;
    left: 0px;
    border-bottom: 1px solid #ddd;
    -webkit-transform: scaleY(.5);
    transform: scaleY(.5);
  }
}
</style>


